<template>
	<NcAppContent>
		<div id="santacloud_admin">
			<h3>{{ t('santacloud', 'SantaCloud Settings') }}</h3>

			<p style="width:300px;font-size: 1em;"><b>{{ t('santacloud', 'Test mode') }}</b></p>
				<div class="toggle-radio" data-style="rounded" data-icons="fontawesome">
					<input type="radio" name="wtpara_test" id="example_rounded_mode1" @change="setM('wtpara_test',1)" v-model="test" :value="1" :checked="test == 1">
					<label for="example_rounded_mode1">{{ t('santacloud', 'ON') }}</label>
					<input type="radio" name="wtpara_test" id="example_rounded_mode2" @change="setM('wtpara_test',2)" v-model="test" :value="2" :checked="test == 2">
					<label for="example_rounded_mode2">{{ t('santacloud', 'OFF') }}</label>
				</div>
				<p style="width:300px;font-size: 0.8em;">{{ t('santacloud', 'Test yes or no. If the test is allowed, the setting for past days is ignored. In the Testmode you can open all doors and an additional notice is displayed.') }}
					<br><b>{{ t('santacloud', 'If Test mode is ON, the Lock mode should also be ON. Otherwise users also can see the content of the doors while you are testing.') }}</b>
					<br><br>
				</p>
				<p style="width:300px;font-size: 1em;"><b>{{ t('santacloud', 'Lock mode') }}</b></p>
				<div class="toggle-radio" data-style="rounded" data-icons="fontawesome">
					<input type="radio" name="wtpara_lock" id="example_rounded_mode12" @change="setM('wtpara_lock',1)" v-model="lock" :value="1" :checked="lock == 1">
					<label for="example_rounded_mode12">{{ t('santacloud', 'ON') }}</label>
					<input type="radio" name="wtpara_lock" id="example_rounded_mode22" @change="setM('wtpara_lock',2)" v-model="lock" :value="2" :checked="lock == 2">
					<label for="example_rounded_mode22">{{ t('santacloud', 'OFF') }}</label>
				</div>
				<p style="width:300px;font-size: 0.8em;">{{ t('santacloud', 'Lock mode yes or no. Lock mode ON means, that users are directed to a please-wait page. OFF means, that users are allowed to see the calendar.') }}
				<br><br>
				</p>
				<p style="width:300px;font-size: 1em;"><b>{{ t('santacloud', 'Past doors') }}</b></p>
				<div class="toggle-radio" data-style="rounded" data-icons="fontawesome">
					<input type="radio" name="wtpara_last" id="example_rounded_mode122" @change="setM('wtpara_last',1)" v-model="last" :value="1" :checked="last == 1">
					<label for="example_rounded_mode122">{{ t('santacloud', 'Yes') }}</label>
					<input type="radio" name="wtpara_last" id="example_rounded_mode222" @change="setM('wtpara_last',2)" v-model="last" :value="2" :checked="last == 2">
					<label for="example_rounded_mode222">{{ t('santacloud', 'No') }}</label>
				</div>
				<p style="width:300px;font-size: 0.8em;">{{ t('santacloud', 'Allow to open doors for past days') }}
				</p>
		</div>
	</NcAppContent>
</template>

<script>
import axios from '@nextcloud/axios'
import { generateUrl } from '@nextcloud/router'
import NcAppContent from '@nextcloud/vue/dist/Components/NcAppContent.js'

export default {
	name: 'App2',
	components: {
		NcAppContent,
	},
	data() {
		return {
			menu: [],
			test: [],
			last: [],
			lock: [],
			wert: [],
			who: []
		}
	},
	/**
	 * Fetch list of notes when the component is loaded
	 */
	async mounted() {
		try {
			const responsep = await axios.get(generateUrl('/apps/santacloud/getparam/wtpara_test'))
			this.test = responsep.data
		} catch (e) {
			console.error(e)
			showError(t('logcleaner', 'Could not fetch settings'))
		}
		try {
			const responsec = await axios.get(generateUrl('/apps/santacloud/getparam/wtpara_last'))
			this.last = responsec.data
		} catch (e) {
			console.error(e)
			showError(t('logcleaner', 'Could not fetch settings'))
		}
		try {
			const responsem = await axios.get(generateUrl('/apps/santacloud/getparam/wtpara_lock'))
			this.lock = responsem.data
		} catch (e) {
			console.error(e)
			showError(t('logcleaner', 'Could not set settings'))
		}
	},
	methods: {
	async setM(who,wert) {
		try {
			await axios.get(generateUrl(`/apps/santacloud/ajax/${who}/${wert}`))
			//if (this.currentNoteId === this.wert) {
			//	this.currentNoteId = null
			//}
		} catch (e) {
			console.error(e)
		}
	},
  }
}
</script>

